@import 'ScssHelpers/helpers-all';

.popup_offer {
	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -5.6rem -2rem 0;
	}
}

.popup_offer__column {
	@include bp($point_2, min) {
		padding: 0 5.6rem 2rem 0;

		&:nth-child(2n+1){
			width: 48.5%;
		}
		&:nth-child(2n+2){
			width: 51.5%;
		}
	}
	@include bp($point_3) {
		width: 100%;

		&:nth-child(2n+1){
			margin-left: calc(var(--gl-indent) * -1);
			width: calc(100% + var(--gl-indent) * 2);
		}

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
}

.popup_offer__image {
	position: relative;

	width: 100%;
	height: 100%;
}

.popup_offer__pic {
	position: relative;

	display: block;
	width: 100%;
	padding-bottom: 105%;
}

.popup_offer__img {
	@include stretch;
	@include cover_img;
}

.popup_offer__content {
	@include bp($point_2, min) {
		padding-top: 3rem;
		max-width: 49.6rem;
	}
}