@import 'ScssHelpers/helpers-all';

.popup {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;

	align-content: stretch;
	width: 100%;
	height: 100%;
	max-height: 100vh;

	overflow-x: hidden;
	overflow-y: auto;
	opacity: 0;
	pointer-events: none;

	background-color: rgba($black_2, .8);

	&.popup--open_state {
		z-index: z-index(popup);

		opacity: 1;
		pointer-events: auto;
	}

	// @include bp($point_3) {
	// 	padding: 3.6rem 2.5rem;
	// }

	@include transition_opacity;
	@include flex_block(row, nowrap, flex-start);
}

.popup_in {
	position: relative;

	width: $page_width;
	max-width: 100%;
	margin: auto;
	background-color: $white;
}

.popup_content {

	@include bp($point_2, min) {
		padding: 6.4rem 8rem;
	}
	@include bp($point_3) {
		padding: 6.4rem var(--gl-indent) 3.6rem;
	}
}

.popup_close {
	position: absolute;
	z-index: 5;
	width: 2.5rem;
	height: 2.5rem;
	cursor: pointer;

	&:hover,
	&:focus{
	}

	@include bp($point_2, min) {
		top: 3rem;
		right: 3.3rem;
	}
	@include bp($point_3) {
		top: 2rem;
		right: 2rem;
	}
}

.popup_close__in {
	position: relative;

	width: 100%;
	height: 100%;

	&::before,
	&::after{
		position: absolute;
		top: 1.2rem;
		left: -.5rem;

		height: 2px;
		width: 3.4rem;

		content:'';
		background-color: $gold_2;

		@include transition_background;

		.popup_close:hover & {
			background-color: $black;
		}
	}

	&::before{
		transform: rotate(45deg);
	}

	&::after{
		transform: rotate(-45deg);
	}
}