@import 'ScssHelpers/helpers-all';

.popup_email {
	display: flex;
	flex-wrap: wrap;
	@include bp($point_3) {
		padding: 6.4rem var(--gl-indent) 3.6rem;
	}
}

.popup_email__column {
	@include bp($point_2, min) {
		&:nth-child(2n+1){
			width: 42.5%;
		}
		&:nth-child(2n+2){
			width: 57.5%;
		}
	}
	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}

		&:nth-child(2n+1){
			margin-left: calc(var(--gl-indent) * -1);
			margin-right: calc(var(--gl-indent) * -1);
			width: calc(100% + var(--gl-indent) * 2);
		}
	}
}

.popup_email__bg {
	position: relative;

	width: 100%;

	@include bp($point_2, min) {
		height: 100%;
		min-height: 65.6rem;
	}
	@include bp($point_3) {
		padding-bottom: 105%;
	}
}

.popup_email__pic {
	display: block;
	width: 100%;
	height: 100%;

	@include stretch;
}

.popup_email__img {
	@include cover_img;
}

.popup_email__content {
	@include bp($point_2, min) {
		padding: 5.4rem 7.6rem 6.2rem 2.8rem;
	}
}

.popup_email__wrapper {
	@include bp($point_2, min) {
		max-width: 49.6rem;
		margin: 0 auto;
	}
}

.popup_email__title {
	color: $black;
	line-height: 1.06;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}

	@include h3;
}

.popup_email__text {
	line-height: 1.5;
	color: $black;
	letter-spacing: -.02em;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}

	@include text;
}

.popup_email__fields {
	&:not(:last-child) {
		margin-bottom: 4.2rem;
	}
}

.popup_email__field {
	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}
