@import "ScssHelpers/helpers-all.scss";

.waiting_form {
	background-color: $gray_3;

	@include bp($point_2, min) {
		padding: 4.8rem 8rem 6.2rem;
	}
	@include bp($point_3) {
		padding: 2.4rem 2rem 3rem;
	}
}

.waiting_form__wrap {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.8rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}
}

.waiting_form__body {
	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;

		margin: 0 -5.5rem -2.6rem 0;
	}
}
		
.waiting_form__item {
	@include bp($point_2, min) {
		padding: 0 5.5rem 2.6rem 0;

		&:not(.waiting_form__item--full_mod) {
			width: 50%;
		}
		&.waiting_form__item--full_mod {
			width: 100%;
		}
	}
	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.waiting_form__button {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}
